<template>
  <aside class="cart-container">
    <div class="cart">
      <h1 class="cart-title spread">
        <span>
          Cart
          <i class="icofont-cart-alt icofont-1x"></i>
        </span>
        <button @click="toggle" class="cart-close">&times;</button>
      </h1>

      <div class="cart-body">
        <table class="cart-table">
          <thead>
            <tr>
              <th><span class="sr-only">Product Image</span></th>
              <th>Product</th>
              <th>Price</th>
              <th>Qty</th>
              <th>Total</th>
              <th><span class="sr-only">Actions</span></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(quantity, key, i) in cart" :key="i">
              <td><i class="icofont-carrot icofont-3x"></i></td>
              <td>{{ key }}</td>
              <td>${{ getPrice(key) }}</td>
              <td class="center">{{ quantity }}</td>
              <td>${{ getPrice(key) * quantity }}</td>
              <td class="center">
                <button
                  @click="remove_cart_item(key)"
                  class="btn btn-light cart-remove"
                >
                  &times;
                </button>
              </td>
            </tr>
          </tbody>
        </table>

        <p class="center" v-if="!Object.keys(cart).length">
          <em>No items in cart</em>
        </p>
        <div class="spread">
          <!-- <span><strong>Total:</strong> \${{ cartTotal }} </span> equivalent to the below line-->
          <span><strong>Total:</strong> ${{ calculateTotal() }} </span>
          <button class="btn btn-light">Checkout</button>
        </div>
      </div>
    </div>
  </aside>
</template>

<script>
export default {
  name: 'SideBar',
  props: ['toggle', 'cart', 'inventory', 'remove_cart_item'],
  computed: {
    cartTotal () {
      const names = Object.keys(this.cart)
      const total = Object.values(this.cart).reduce((acc, curr, index) => {
        return acc + curr * this.getPrice(names[index])
      })

      return total.toFixed(2)
    }
  },
  methods: {
    getPrice (name) {
      const prod = this.inventory.find((p) => p.name === name)
      return prod.price.USD
    },
    calculateTotal () {
      // const names = Object.keys(this.cart)
      // const total = Object.values(this.cart).reduce((acc, curr, index) => {
      //   return acc + (curr * this.getPrice(names[index]))
      // }, 0)

      // each time looping through curr = [key, value]
      const total = Object.entries(this.cart).reduce((acc, curr, index) => {
        console.log(curr)
        return acc + curr[1] * this.getPrice(curr[0])
      }, 0)

      return total.toFixed(2)
    }
  }
}
</script>
